<template>
  <v-app class="surface-base apply">
    <v-main class="py-0">
      <v-container>
        <v-row>
          <v-col>
            <v-card max-width="500" class="mx-auto text-center">
              <v-card-title class="font-weight-light grey--text">
                Create Account
              </v-card-title>
              <v-card-text class="pb-0">
                <v-divider></v-divider>
              </v-card-text>
              <v-card-text>
                <!-- START:__FORM::INPUTS__ -->
                <v-form
                  @submit.prevent="register"
                  action="/"
                  ref="form"
                  id="nativeForm"
                >
                  <v-card-text class="pb-0">
                    <v-text-field
                      v-model="firstName"
                      :rules="[rules.required]"
                      outlined
                      label="First Name"
                    ></v-text-field>
                    <v-text-field
                      v-model="lastName"
                      :rules="[rules.required]"
                      outlined
                      label="Last Name"
                    ></v-text-field>
                    <v-text-field
                      v-model="email"
                      :rules="[rules.required, rules.email]"
                      outlined
                      label="Email"
                    ></v-text-field>
                    <v-text-field
                      v-model="password"
                      :rules="[rules.required]"
                      outlined
                      :type="showPass ? 'text' : 'password'"
                      :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
                      @click:append="showPass = !showPass"
                      label="Password"
                    ></v-text-field>
                  </v-card-text>
                  <!-- START:__FORM::SUBMIT__ -->
                  <v-card-actions class="px-4">
                    <v-btn
                      x-large
                      block
                      type="submit"
                      :loading="loading"
                      class="py-5 white--text text-capitalize"
                      color="#16bf82"
                    >
                      <strong>Register</strong>
                    </v-btn>
                  </v-card-actions>
                  <v-card-text class="pb-0">
                    <v-checkbox
                      color="primary"
                      label="Keep me up to date on news and exclusive offers"
                    ></v-checkbox>
                  </v-card-text>
                  <div link class="mt-3 text--primary font-weight-light">
                    <v-card-text>
                      <router-link to="/login">
                        Already have an account? click here</router-link
                      >
                    </v-card-text>
                  </div>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    loading: false,
    showPass: false,
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    rules: {
      required: (v) => !!v || "required",
      nameLength: (v) => /\b\w{3,10}\b/.test(v) || "more than 3 & less than 10",
      email: (v) =>
        /\b\w{2,20}\b@\b\w{1,15}\b\.\b\w{1,10}\b/.test(v) || "invalid email",
    },
  }),
  methods: {
    register() {
      let nativeForm = document.getElementById("nativeForm");
      if (this.$refs.form.validate()) {
        this.loading = true;
        localStorage.setItem(
          "snack",
          JSON.stringify({
            text: "Account Created",
            active: true,
            color: "success",
          })
        );
        localStorage.setItem(
          "user-info",
          JSON.stringify({
            firstName: this.firstName,
            lastName: this.lastName,
            email: this.email,
            password: this.password,
            id: (Math.random().toString(36) + Date.now().toString(36)).substr(
              2
            ),
          })
        );
        localStorage.setItem(
          "login-user-info",
          JSON.stringify({
            firstName: this.firstName,
            LastName: this.LastName,
            email: this.email,
            password: this.password,
            id: (Math.random().toString(36) + Date.now().toString(36)).substr(
              2
            ),
          })
        );
        setTimeout(() => {
          nativeForm.submit();
          this.loading = false;
        }, 1500);
      }
    },
  },
  computed: {
    ...mapState({
      snack: (store) => store.ecommerce.snack,
    }),
  },
};
</script>

